import { hillo, IKDataEntity } from 'innerken-utils'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'ID',
    tableConfig: {
      align: 'start',
    },
  },
  orderId: {
  },
  createdAt: {
  },
  amount: {
  },
}

const config = {
  load: async function (filter) {
    return (await hillo.get('MemberCard.php', Object.assign({
      op: 'getOne',
      withConsumeLog: true,
    }, ...filter)))
      .content.consumeLog.map(function (item) {
        return IKDataEntity.parseDataForEntity(item, entity)
      })
  },
}

export default IKDataEntity.ModelFactory(entity, config)
